<template>
    <div>
        <el-tabs type="card">
            <el-tab-pane label="首页">
                <section class="sub-nav-box">
                    <div class="sub-nav-list fn-clear">
                        <router-link :to="{ name: 'BasicQuotaType' }" class="-item fn-left">
                            <i>
                                <img src="../../assets/images/home/basic_quota_type.png" alt="">
                            </i>
                            <span>基础指标类别</span>
                        </router-link>
                        <router-link :to="{ name: 'BasicQuota' }" class="-item fn-left">
                            <i>
                                <img src="../../assets/images/home/basic_quota.png" alt="">
                            </i>
                            <span>基础指标</span>
                        </router-link>
                        <router-link :to="{ name: 'DeriveQuota' }" class="-item fn-left">
                            <i>
                                <img src="../../assets/images/home/derive_quota.png" alt="">
                            </i>
                            <span>衍生指标</span>
                        </router-link>
                        <router-link :to="{ name: 'QuotaSettings' }" class="-item fn-left">
                            <i>
                                <img src="../../assets/images/home/quota_settings.png" alt="">
                            </i>
                            <span>指标配置管理</span>
                        </router-link>
                    </div>
                </section>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
    export default {
        name: 'Home',
        mixins: [],
        components: {},
        data() {
            return {}
        },
        beforeRouteEnter(to, from, next) {
            next();
        },
        created() {
        },
        mounted() {
            this.calculateSize();
            window.onresize = () => {
                this.calculateSize();
            }
        },
        computed: {},
        methods: {
            /***
             * 计算tab content的最小高度
             */
            calculateSize() {
                const clientW = window.innerWidth;
                const clientH = window.innerHeight;
                // console.log(clientW);
                // 75 - 系统顶部条高度
                // 40 - 主面板上下padding相加之和
                // 15 - tab的head的margin-bottom的值
                // 49 - tab的head的高度
                document.querySelector('.el-tabs__content').style.minHeight = (clientH - 75 - 40 - 15 - 49 ) + 'px';
            }
        },
        filters: {},
        watch: {},
        destroyed() {
        },
        beforeRouteLeave(to, from, next) {
            next();
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/styles/variables";

    .sub-nav-box {
        text-align: center;
        .sub-nav-list{
            margin: 50px auto 0;
            width: 1070px;
        }
        .-item {
            width: 250px;
            height: 250px;
            border: 1px solid $blue-lighter;
            border-radius: 10px;
            text-align: center;
            &:not(:last-child) {
                margin-right: 20px;
            }
            i {
                margin-bottom: 10px;
                display: block;
            }
            span {
                font-size: 22px;
                font-weight: bold;
            }
        }
    }
</style>

<style lang="scss">

</style>



